* {
    background: #f7f7f7;
    font-family: "Poppins", sans-serif;
    font-size: 62.5%;
  
  }

.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.7rem 10%;
    height: 6.5rem;
    background-color: #fff;
    box-shadow: 0 1px 4px rgba(146, 161, 176, 0.3);
    
  }
  .section {
    min-height: 100vh;
    padding: 2rem 9%;
  }
  .heading {
    font-size: 3.5rem;
    color: rgb(32, 32, 32);
    font-weight: 800;
    text-align: center;
  }
   span {
    color: rgb(115, 3, 167);
  }
   .logo {
    font-size: 1.9rem;
    font-weight: 800;
    text-decoration: none;
    color: #0e2431;
  }
   .logo i {
    font-size: 2.2rem;
  }
   .logo:hover {
    color: #fc8c05;
  }
   .navbar ul {
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
  }
   .navbar li {
    margin-left: 2.5rem;
  }
   .navbar ul li a {
    font-size: 1.57rem;
    color: #0e2431;
    font-weight: 600;
    text-decoration: none;
    letter-spacing: 0.04rem;
    transition: 0.2s;
  }
   .navbar ul li a.active,
   .navbar ul li a:hover {
    color: #c72b43;
    border-bottom: 0.2rem solid #ca3047;
    padding: 0.5rem 0;
  }